@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
    margin: 0;
    padding: 0;
    vertical-align: baseline;
}

body { 
    text-align: center;
    padding: 0 40px;
}

body > div {
    text-align: left;
    display: inline-block;
    clear: both;
}

div.site-wrapper {
    font-family: 'Roboto', sans-serif;
    margin: 0;
}

div.container {
    float: left;
    width: 1000px;
    border-left: 1px solid lightseagreen;
    border-right: 1px solid lightseagreen;
}
.aside-menu {
    float: left;
    width: 160px;
}

.aside-menu nav a {
    display: block;
    margin: 10px 0px;
    text-decoration: none;
    font-size: 22px;
    color: rgb(17, 65, 62);
    font-weight: bold;
}

.aside-menu nav a:hover {
    text-decoration: underline;
}

.aside-menu img {
    width: 128px;
    height: 128px;
}
.site-header {
    padding: 10px;
    margin-bottom: 20px;
    text-align: right;
    background-color: rgb(207, 237, 247);
}

.site-header li{
    display: inline-block;
    padding: 10px;
}

.site-header li span {
    font-size: 20px;
}

.site-header a {
    text-decoration: none;
    margin-right: 5px;
    color: black;
    font-size: 19px;
    font-weight: bold;
}

.site-header a:hover {
    color: rgb(97, 218, 251);
}
.main-wrapper { 
    padding: 0 20px;
}

.main-wrapper section {
    padding: 0px 10px;
    width: 98%;
}

.main-wrapper section h2 {
    font-size: 30px;
    padding: 10px;
    margin: 0 -10px;
}

.main-wrapper input[type=text], input[type=password], input[type=submit] {
    display: block;
    margin: 10px 0px;
    padding: 5px;
}

.main-wrapper input[type=submit] {
    padding: 5px 10px;
    font-size: 16px;
}

.main-wrapper label {
    display: block;
    margin-top: 20px;
    font-size: 18px;
}
.home-article { 
    padding: 20px 0 10px 0;
}

.home-article h3 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 22px;
    margin-bottom: 20px;
}

.home-article h3 a {
    text-decoration: none;
    color: black;
}

.home-article p.description {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 17px;
    margin-bottom: 23px;
}

.home-article a.read-more {
    text-decoration: none;
}
.error-message {
    background: #ecb3b3;
    text-align: center;
    padding: 10px;
    font-family: cursive;
    font-size: 15px;
    color: #24809e;
}
.success-message {
    background: #b1fac1;
    text-align: center;
    padding: 10px;
    font-family: cursive;
    font-size: 15px;
    color: #24809e;
}
.create-article input[name=title], input[name=imageUrl], input[name=youtubeUrl] {
    width: 500px;
}

.create-article textarea[name=content] {
    resize: vertical;
    width: 70%;
    min-height: 300px;
    margin-top: 10px;
}

.create-article select[name=category] {
    margin-top: 10px;
    width: 140px;
    height: 26px;
}
.article-form input[name=title], input[name=imageUrl], input[name=youtubeUrl] {
    width: 500px;
}

.article-form textarea[name=content] {
    resize: vertical;
    width: 70%;
    min-height: 300px;
    margin-top: 10px;
}

.article-form select[name=category] {
    margin-top: 10px;
    width: 140px;
    height: 26px;
}

.article-form input[name=image] {
    margin-top: 10px;
}

.article-form img {
    display: block;
    width: 300px;
    height: 282px;
    object-fit: cover;
}
.details-article {
    padding: 10px 0;
}

.details-article h3 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 22px;
    display: inline-block;
    margin-bottom: 25px;
}

.details-article h4 {
    margin-top: 35px;
    text-decoration: underline;
}

.details-article img {
    float: left;
    margin-right: 90px;
    width: 300px;
    height: 282px;
    object-fit: cover;
}

.details-article iframe {
    border: 0;
}

.details-article div.visual-section {
    /* height: 300px; */
    margin-bottom: 12px;
}

.details-article p.description {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 17px;
    margin-bottom: 20px;
}

.details-article a.read-more {
    text-decoration: none;
}

.details-article div.user-control {
    display: inline-block;
    float: right;
}

.details-article a.user-btn {
    text-decoration: none;
    padding: 0 10px;
    font-size: 17px;
    font-weight: bold;
    color: black;
}

.details-article a.user-btn:hover {
    color: rgb(97, 218, 251);
}

.details-article span.author-name {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
}

.details-article time[datetime] {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 13px;
}

.details-article form {
    margin-top: 20px;
}

.details-article label {
    font-size: 16px;
    display: block;
    margin: 10px 0;
}

.details-article input[type=submit] {
    padding: 5px;
    font-size: 14px;
}

.details-article input[name=name] {
    width: 487px;
}

.details-article textarea[name=comment] {
    width: 500px;
}

.details-article div.article-info {
    margin-top: 40px;
}

.details-article span.cannot-like-article {
    float: right;
    margin-top: 12px;
    color: #32a999;
    font-weight: bold;
}

.details-article span.cannot-like-article span {
    color: #dc687e;
}

.details-article span.can-like-article {
    float: right;
    margin-top: 12px;
    color: #04808c;
    font-weight: bold;
}

.details-article span.can-like-article span {
    font-size: 19px;
    color: #dc687e;
    margin: 0 5px;
}

.details-article span.can-like-article span:hover {
    color: #4de07e;
    cursor: pointer;
}
.comment-box {
    margin: 15px 0;
    font-family: Helvetica, sans-serif;
    padding: 0 0 10px 5px;
    background-color: #f4fffe;
    border-radius: 3px;
    border: 1px solid #64c0c3;
    max-width: 500px;
}

.comment-box div.comment-name {
    display: inline-block;
    color: #6085c6;
    font-weight: bold;
}

.comment-box var.comment-number {
    font: 600 15px Arial,serif;
    color: #33a298;
    display: inline-block;
    padding: 5px 5px 0 0;
}

.comment-box time.comment-date {
    display: inline-block;
    font-size: 12px;
    float: right;
    margin: 5px;
}

.comment-box p.comment-description {
    margin-top: 15px;
}
.article-delete h3 {
    margin: 30px 0;
}

.article-delete a {
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    color: black;
    background: #cfedf7;
    padding: 8px 20px;
    border-radius: 20px;
    margin-right: 15px;
}

.article-delete a:hover {
    color: rgb(87, 197, 224);
    cursor: pointer;
}
.user-profile {
    text-align: center;
}

.user-profile i {
    font-size: 40px;
    color: #5cbcd9;
}

.user-profile h4 {
    font-size: 18px;
    margin-bottom: 12px;
}

.user-profile ul {
    list-style-type: none;
    margin: 5px 0 15px 0;
}

.user-profile ul li {
    margin: 3px 0;
}

.user-profile ul li a {
    font-size: 17px;
}
footer {
    text-align: center;
    padding: 40px 20px 0 20px;
}
